* {
    font-size: 16px;
    font-family: 'Noto Sans KR', sans-serif;
    letter-spacing: -0.8px;
    font-weight:400;
    line-height:1.7;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
}

.popHeader {
    padding: 15px 110px 15px 25px;
    background-color: #ebf3ff;
}

.popHeader > h1 {
    color: #000;
    font-size: 20px;
    font-weight: 700;
    margin:0;
}

.pop_content {
    padding: 24px;
}

.pop_tabList {
    margin-bottom: 20px;
    border-bottom:1px solid #dcdddf;
    text-align: center;
}

.pop_tabList ul {
    text-align: left;
}

.pop_tabList li{
    display: inline-block;
    position:relative;
    padding-left: 0;
}

.pop_tabList li span {
    display: block;
    font-size: 17px;
    padding: 10px 22px;
    position: relative;
    border-top: 1px solid #cfcfcf;
    border-left: 1px solid #cfcfcf;
    border-right: 1px solid #cfcfcf;
    border-radius: 8px 8px 0 0;
    cursor: pointer;
    color:#555;
    background-color: #f6f6f6;
    font-weight:600;
}

.pop_tabList li span:hover {
    color:#1d91e7;
}

.pop_tabList li span.on {
    color: #1d91e7;
    border-bottom: none;
    text-decoration: underline;
    background-color: #fff;
    font-weight: 600;
}

.pop_tabList li span.on::after {
    content: "";
    position: absolute;
    width: 100%;
    background-color: #fff;
    height: 2px;
    bottom: -1px;
    left: 0;
    z-index: 10;
}

.pop_tab_box {
    border-bottom: 8px solid #f5f5f5;
    white-space: nowrap;
    overflow-x: auto;
}

.pop_tab_box ul {
    padding-bottom: 20px;
    border-bottom: 1px solid #dedede;
}

.pop_tab_box li {
    display: inline-block;
}

.pop_tab_box li span {
    display: block;
    border-radius: 17.5px;
    background-color: #f1f1f1;
    padding: 0 17px;
    line-height: 33px;
    margin: 2px;
    color: #333333;
    font-size: 15px;
}
.pop_tab_box li span.on {
    background-color: #66676b;
    color: #fff;
    box-shadow: rgb(0 0 0 / 13%) 0px 0px 5px;
}

/* 서브 컨텐츠 */
.sub_tab_content {
    border-bottom: 8px solid #f5f5f5;
}

.sub_tab_content ul {
    padding: 30px 0;
    border-bottom: 1px solid #dedede;
}

.sub_tab_content li > span {
    display: inline-block;
    padding-left: 20px;
    font-size: 16px;
    color: #999999;
    min-width: 60px;
}

.sub_tab_content li + li {
    margin-top:10px;
}

.sub_tab_content li > span.popH1 {
    background: url(./img/pop_ho01.png) no-repeat left center;
}

.sub_tab_content li > span.popH2 {
    background: url(./img/pop_ho02.png) no-repeat left center;
}

.sub_tab_content li > span.popH3 {
    background: url(./img/pop_ho03.png) no-repeat left center;
}

.sub_tab_content li > span.popH4 {
    background: url(./img/pop_ho04.png) no-repeat left center;
}

.sub_tab_content li > p {
    margin-left: 15px;
    color: #000000;
    font-weight: 500;
    font-size: 16px;
    width: calc(100% - 98px);
    vertical-align: top;
}

.urlNone {
    display: inline-block;
    color: #1d91e7;
    font-size: 16px;
    text-decoration: underline;
}

/* 병원 정보 FOOTER */
.infoFooter {
    background-color: #f6f8f9;
    padding: 20px;
    margin-top:40px;
}

.infoFooter > div {
    color:#000;
    font-weight:600;
    margin-bottom:5px;
}

.icon-quick {
    width: 21px;
    height: 19px;
    background-image: url(./img/quick.png);
    margin-left: 3px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: center center;
    vertical-align: middle;
}

.infoClose {
    border-top: #e7e7e7 solid 1px;
    text-align: right;
    padding: 10px 24px;
}

.infoClose > button {
    border: #e4e5e7 solid 1px;
    color: #000;
    font-size: 15px;
    line-height: 33px;
    padding: 0 30px 0 12px;
    background: url(./img/close.png) calc(100% - 10px) center no-repeat;
}

.infoClose > button:hover {
    border-color: #58606b;
}

.infoClose > button > span {
    color:#000;
    font-size:15px;
    line-height:33px;
}

/* 의사 현황 */
.doctorTitle {
    margin: 35px 0 15px;
    font-size: 18px;
    font-weight: bold;
}

.doctorTable {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
    margin-top: 15px;
    border-top: 1px solid #212121;
}

.doctorTable th {
    text-align: center;
    border-bottom: 1px solid #d4d4d6;
    border-left: 1px solid #d4d4d6;
    padding: 10px 15px;
    background: #f7f8fa;
    color: #000000;
    font-weight: 700;
    word-break: break-all;
    margin-bottom: 2px;
    margin-top: 2px;
}

.doctorTable td {
    background-color: #fff;
    text-align: center;
    border-bottom: 1px solid #d4d4d6;
    border-left: 1px solid #d4d4d6;
    padding: 10px 15px;
}

.doctorUl {
    text-align: left;
    word-break: break-all;
    margin-bottom: 2px;
    margin-top: 2px;
    list-style-type: disc;
}

.doctorUl li {
    width: 49%;
    text-align: left;
    padding-left: 6px;
    position: relative;
    vertical-align: top;
    display: inline-block;
    word-break: break-all;
    margin-bottom: 2px;
    margin-top: 2px;
}

.doctorUl li:after {
    position: absolute;
    content: '';
    display: block;
    left: 0;
    top: 11px;
    width: 2px;
    height: 2px;
    background: #434343;
}

/* 평가 정보 css */
.evaluationBox {
    background-color: #f4f5f7;
    padding: 20px;
    border-radius: 5px;
    margin-top: 30px;
}

.evaluationBox > ul {
    color: #666;
}

.evaluationBox li {
    position: relative;
    padding: 0 0 0px 10px;
    letter-spacing: -0.5px;
    word-break: keep-all;
    line-height: 24px;
}

.evaluationBox li::after {
    position: absolute;
    content: '';
    display: block;
    left: 0;
    top: 11px;
    width: 3px;
    height: 3px;
    background: #707070
}

/* 질환 제목 */
.illnessTit {
    background: url(img/illnessTit.png) no-repeat 0 8px;
    margin-top: 35px;
    margin-bottom: 10px;
    font-size: 17px;
    line-height: 22px;
    color: #000;
    font-weight: 500;
    padding: 0 0 0 13px;
}

/* 질환 목록 데이터들 */
.pop_img {
    padding-bottom: 30px;
    border-bottom: 1px solid #e6e6e6;
    margin-left: 0;
}

.pop_img > img {
    max-width: 100%;
    vertical-align: middle;
}

.pop_img > ul {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 113px);
    margin-left: 10px;
}

.pop_img li {
    display: inline-block;
    width: 48%;
    border-bottom: 1px dashed #e0e0e0;
    padding: 13px 0 10px 15px;
    vertical-align: top;
    background: url(img/pop_icon.png) no-repeat left 21px;
    color: #000000;
    font-weight: 400;
    word-break: keep-all;
}

.pop_img li:nth-child(2n) {
    margin-left: 20px;
}

.pop_img li span {
    display: inline-block;
    color: #333333;
    font-weight: 400;
    padding-left: 35px;
    line-height: 27px;
    float: right;
}

/* 평가 정보에 따른 css */
.grade {
    position:relative;
    cursor: pointer;
}

.grade::after{
    display: block;
    width: 27px;
    height: 27px;
    vertical-align: middle;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 100%;
    position: absolute;
    content: '';
    left: 0;
    top: 50%;
    margin-top: -14.25px;
}

/* 양호 등급 */
.evaluationGood::after {
    background-image: url(img/good_icon.png);
}

/* 평가 등급 1등급 */
.evaluationRating1::after {
    background-image: url(img/first.png);
}

/* 평가 등급 2등급 */
.evaluationRating2::after {
    background-image: url(img/two.png);
}

/* 평가 등급 3등급 */
.evaluationRating3::after {
    background-image: url(img/three.png);
}

/* 평가 등급 4등급 */
.evaluationRating4::after {
    background-image: url(img/four.png);
}

/* 평가 등급 5등급 */
.evaluationRating5::after {
    background-image: url(img/five.png);
}